<template>
<b-overlay :show="overlay" rounded="sm">
    <div id="app">
        
        <b-row class="view-button">
            <b-col lg="3">
                <b-row class="button-padding">
                    <b-button block :class="[ data_type === 'all' ? 'selected' : '']" class="text-capitalize" variant="dark" @click="change_source('all')">All</b-button>
                </b-row>
            </b-col>
            <b-col lg="3">
                <b-row class="button-padding">
                    <b-button block :class="[ data_type === 'current' ? 'selected' : '']" class="text-capitalize" variant="dark" @click="change_source('current')">Current</b-button>
                </b-row>
            </b-col>
            <b-col lg="3">
                <b-row class="button-padding">
                    <b-button block :class="[ data_type === 'history' ? 'selected' : '']" class="text-capitalize" variant="dark" @click="change_source('history')">History</b-button>
                </b-row>
            </b-col>
            <b-col lg="3">
                <b-row class="button-padding">
                    <b-button block class="text-capitalize" variant="dark" @click="getData">Reload</b-button>
                </b-row>
            </b-col>
            <h6 class="tweet_date">Date : {{nowdate}}</h6>
            <b-col lg="12" class="col-margin">
                <b-row>
                    <b-button v-b-toggle.collapse-region variant="dark" block class="">Filter</b-button>
                </b-row>
            </b-col>
        </b-row>
        <div class="view-card">
            <b-collapse id="collapse-region">
                <b-card class="tweet_region">
                    <b-form-group v-slot="{ ariaDescribedby }">
                        <b-form-checkbox-group
                            id="checkbox-group-1"
                            v-model="checkbox_selected"
                            :options="region"
                            :aria-describedby="ariaDescribedby"
                            name="flavour-1"
                            switches
                        ></b-form-checkbox-group>
                    </b-form-group>
                    <b-row><b-col class="col-margin"></b-col></b-row>
                    <b-row>
                        <b-col lg="6">
                            <b-row class="button-padding">
                                <b-button block variant="outline-secondary" @click="control_checkbox('select')">Select All</b-button>
                            </b-row>
                        </b-col>
                        <b-col lg="6">
                            <b-row class="button-padding">
                                <b-button block variant="outline-secondary" @click="control_checkbox('release')">Release All</b-button>
                            </b-row>
                        </b-col>
                    </b-row>
                </b-card>
            </b-collapse>
        </div>
        <div v-if="(data_type=='all' || data_type=='current') && !hasitems(jsondata.current)">
            <h3 class="tweet_title list_title">Current</h3>
            <div class="tweet">
                No data!
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='current') && hasitems(jsondata.current)">
            <h3 class="tweet_title list_title">Current</h3>
            <div v-for="item in jsondata.current" v-bind:key="item.id">
                <div class="tweet" v-if="item_filter(item.service)">
                    <div class="box">
                        <article class="media">
                            <div class="media-left">
                                <figure class="image">
                                    <img src="">
                                </figure>
                            </div>
                            <div class="media-content">
                                <div class="content">
                                    <p><strong>{{item.service_name}}</strong> <small>{{item.service}}</small></p>
                                    <p>{{item.summary}}</p>
                                </div>
                            </div>
                        </article>
                    </div>
                </div>
            </div>
            <div v-if="checkbox_selected.length == 0">
                <div class="tweet">
                    No data!
                </div>
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='history') && !hasitems(jsondata.archive)">
            <h3 class="tweet_title list_title">History</h3>
            <div class="tweet">
                No data!
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='history') && hasitems(jsondata.archive)">
            <h3 class="tweet_title list_title">History</h3>
            <div v-for="item in jsondata.archive" v-bind:key="item.id">
                <div class="tweet" v-if="item_filter(item.service)">
                    <div class="box">
                        <article class="media">
                            <b-row>
                                <b-col class="content" cols="10">
                                    <p><strong>{{item.service_name}}</strong></p>
                                    <p>Service : {{item.service}}</p>
                                    <p>Date : {{ $moment(item.date, 'X').format('YYYY-MM-DD HH:mm:ss Z') }}
                                    <p>{{item.summary}}</p>
                                </b-col>
                                <b-col cols="2" class="padding-left-0">                               
                                    <b-button v-b-modal="item.date" class="text-capitalize"><b-icon icon="zoom-in"></b-icon></b-button>
                                    <b-modal :id="item.date" size="md" scrollable hide-header ok-only centered header-bg-variant="dark" header-text-variant="light"><span id="modalhtml" v-html="item.description"></span></b-modal>
                                </b-col>
                            </b-row>
                        </article>
                    </div>
                </div>
            </div>
            <div v-if="checkbox_selected.length == 0">
                <div class="tweet">
                    No data!
                </div>
            </div>
        </div>
        <div>
            <a class="ScrollOnTop btn btn-light" @click="ScrollOnTop" v-show="visible_scroll">
                <b-icon-arrow-up></b-icon-arrow-up>
            </a>
        </div>

    </div>
</b-overlay>
</template>

<script>

import DataList from '../util/getData'

export default {
	name: 'DataList',
	components: {
        
	},
    data() {
        return {
            jsondata:{},
            data_type: 'all',
            overlay: false,
            nowdate: '',
            visible_scroll: false,
            visible_collapse: false,
            checkbox_selected: ["global", "us-east-1", "us-east-2", "us-west-1", "us-west-2", "af-south-1", "ap-east-1", "ap-south-1", "ap-northeast-3", "ap-northeast-2", "ap-southeast-1", "ap-southeast-2", "ap-northeast-1", "ca-central-1", "eu-central-1", "eu-west-1", "eu-west-2", "eu-south-1", "eu-west-3", "eu-north-1", "me-south-1", "sa-east-1"],
            region: [
                {"text":"Global Service","value":"global"},
                {"text":"미국 동부 (버지니아 북부) / us-east-1","value":"us-east-1"},
                {"text":"미국 동부 (오하이오) / us-east-2","value":"us-east-2"},
                {"text":"미국 서부 (캘리포니아) / us-west-1","value":"us-west-1"},
                {"text":"미국 서부 (오레곤) / us-west-2","value":"us-west-2"},
                {"text":"아프리카 (케이프타운) / af-south-1","value":"af-south-1"},
                {"text":"아시아 태평양 (홍콩) / ap-east-1","value":"ap-east-1"},
                {"text":"아시아 태평양 (뭄바이) / ap-south-1","value":"ap-south-1"},
                {"text":"아시아 태평양 (오사카) / ap-northeast-3","value":"ap-northeast-3"},
                {"text":"아시아 태평양 (서울) / ap-northeast-2","value":"ap-northeast-2"},
                {"text":"아시아 태평양 (싱가포르) / ap-southeast-1","value":"ap-southeast-1"},
                {"text":"아시아 태평양 (시드니) / ap-southeast-2","value":"ap-southeast-2"},
                {"text":"아시아 태평양 (도쿄) / ap-northeast-1","value":"ap-northeast-1"},
                {"text":"캐나다 (중부) / ca-central-1","value":"ca-central-1"},
                {"text":"유럽 (프랑크푸르트) / eu-central-1","value":"eu-central-1"},
                {"text":"유럽 (아일랜드) / eu-west-1","value":"eu-west-1"},
                {"text":"유럽 (런던) / eu-west-2","value":"eu-west-2"},
                {"text":"유럽 (밀라노) / eu-south-1","value":"eu-south-1"},
                {"text":"유럽 (파리) / eu-west-3","value":"eu-west-3"},
                {"text":"유럽 (스톡홀름) / eu-north-1","value":"eu-north-1"},
                {"text":"중동 (바레인) / me-south-1","value":"me-south-1"},
                {"text":"남아메리카 (상파울루) / sa-east-1","value":"sa-east-1"}
            ]
        }
    },
    mounted() {
        this.getData()
        window.addEventListener('scroll', this.scrollListener)
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.scrollListener)
    },
    methods: {
        async getData() {
            this.toggle_overlay(true)
            this.jsondata = await DataList.get_data()
            this.nowdate = this.date()
            // console.log("component:",this.jsondata)
            this.toggle_overlay(false)
        },
        date() {
            return this.$moment().format('YYYY-MM-DD HH:mm:ss Z')
        },
        change_source(data_type) {
            this.data_type = data_type;
        },
        hasitems(data) {
            try {
                if(data.length > 0) return true
                else return false
            } catch {
                return false
            }
        },
        toggle_overlay(value) {
            this.overlay = value
        },
        ScrollOnTop() {
            window.scroll(0, 0)
        },
        scrollListener(e) {
            this.visible_scroll = window.pageYOffset > 150
        },
        control_checkbox(value) {
            if(value == 'select') {
                this.checkbox_selected = ["global", "us-east-1", "us-east-2", "us-west-1", "us-west-2", "af-south-1", "ap-east-1", "ap-south-1", "ap-northeast-3", "ap-northeast-2", "ap-southeast-1", "ap-southeast-2", "ap-northeast-1", "ca-central-1", "eu-central-1", "eu-west-1", "eu-west-2", "eu-south-1", "eu-west-3", "eu-north-1", "me-south-1", "sa-east-1"]
            } else if(value == 'release') {
                this.checkbox_selected = []
            }
        },
        item_filter(service) {
            for(var item in this.checkbox_selected) {
                if((service.match(/-/g)||[]).length == 3) {
                    // case: region
                    let region = service.substring(service.indexOf('-')+1,service.length)
                    if(region.includes(this.checkbox_selected[item])) return true
                } else {
                    // case: global
                    if(this.checkbox_selected[item] == 'global') return true
                }
            }
            return false
        }
    }
}
</script>

<style>
    .greybg {
        background-color: #eeeeee;
    }

    .whitebg {
        background-color: #ffffff;
    }

    .yellowbg {
        background-color: #fcfdef;
    }

    .yellowfg {
        color: #997744;
    }

    .greyfg {
        color: #444444;
    }

    .orangefg {
        color: #dd6622;
    }

    #modalhtml div {
        margin-bottom: 10px;
    }

    .list_title {
        font-weight: 800;
        line-height: 1.2;
    }
    /* .btn {
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.55);
        padding-top: 5px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 5px;
    } */
    .ScrollOnTop {
        position: fixed;
        right: 0px;
        margin-right: 20px;
        top: 90vh;
    }
</style>