import axios from 'axios'

const server = process.env.NODE_ENV === 'production' ? 'https://status.aws.amazon.com/data.json' : 'data.json'

// axios.defaults.headers.get['Content-Type'] = 'application/json;charset=utf-8';
// axios.defaults.headers.get['Access-Control-Allow-Origin'] = '*';
// axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
// axios.defaults.headers.get['Accept'] = 'text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.9';

export default class getData {
    static async get_data() {
        const options = {
            url: server,
            method: 'GET',
            Headers: {
                // 'Content-Type': 'application/x-www-form-urlencoded',
                // 'Access-Control-Request-Method': 'POST',
                // 'Access-Control-Request-Headers': 'X-PINGOTHER, Content-Type'
                // "Accept":"text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.9"
            },
            // mode: 'no-cors'
        }
        this.data = await axios_get(options)
        this.archive = this.data.archive.reverse()
        this.current = this.data.current.reverse()
        this.data_length = this.archive.length
        console.log(this.archive)
        console.log(this.data_length)

        return {'archive':this.archive, 'current':this.current}
    }
}

function axios_get(param) {
    return new Promise((resolve, reject) => {
        axios(param).then(res => {
            console.log("response header",res.headers)
            console.log("promise:",res.data)
            resolve(res.data)
        }).catch(err => {
            console.log("network error:"+err)
            reject("err")
        })
    })
}